body {
  font: 14px "ヒラギノ明朝 ProN W6" "Century Gothic", Futura, sans-serif;
  margin: 20px;
}
html,
body,
div,
span,
applet,
object,
h1,
video {
  margin: 0;
  padding: 0;
  border: 0;
}
ol,
ul {
  padding-left: 30px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
.background {
  width: 700px;
  height: 100%;
  background: #ffdead;
  z-index: -1;
}

.shopping-list {
  padding: 10px 30px;
}
h1 {
  color: #ff6347;
  padding-bottom: 15px;
}

h1:after {
  content: "";
  display: block;
  height: 4px;
  background: -webkit-linear-gradient(to right, rgb(230, 90, 90), transparent);
  background: linear-gradient(to right, rgb(50, 179, 161), transparent);
}
.a {
  border: 0;
  background-color: #ffdead;
}
.b {
  border: 0;
  background-color: #ffdead;
}
.c {
  border: 0;
  background-color: #ffdead;
}

p {
  text-align: center;
  margin-top: 10px;
  /*color:#008080*/
}

h2 {
  color: #20b2aa;
  font-size: 28px;
  margin-bottom: 10px;
}
h2:after {
  content: "";
  display: block;
  height: 4px;
  background: -webkit-linear-gradient(to right, rgb(230, 90, 90), transparent);
  background: linear-gradient(to right, rgb(230, 90, 90), transparent);
}

.drink {
  padding: 0 !important;
  margin-right: 100%;
  margin-bottom: 10px;
}
.drink img {
  border-radius: 15px;
}

.c p {
  font: 8px "Century Gothic", Futura, sans-serif;
  margin: 0;
}

.price p {
  text-align: left;
  color: black;
  font-size: 8px;
}

.sum p {
  text-align: left;
  color: black;
  background-color: white;
  margin: 0;
}
.sum p {
  position: relative;
  line-height: 1.4;
  padding: 0.25em 1em;
  display: inline-block;
}

.sum p:before,
.sum p:after {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  display: inline-block;
}

.sum p:before {
  border-left: solid 2px #ff5722;
  border-top: solid 2px #ff5722;
  top: 0;
  left: 0;
}

.sum p:after {
  border-right: solid 2px #ff5722;
  border-bottom: solid 2px #ff5722;
  bottom: 0;
  right: 0;
}

.price p:first-child {
  padding-top: 10px;
}

.a {
  margin: 0 auto;
  overflow: hidden;
}
.a img {
  width: 100%;
  cursor: pointer;
  transition-duration: 0.5s;
}
.a img:hover {
  transform: scale(1.1);
  transition-duration: 0.5s;
  opacity: 0.7;
}

.b {
  margin: 0 auto;
  overflow: hidden;
}
.b img {
  width: 100%;
  cursor: pointer;
  transition-duration: 0.5s;
}
.b img:hover {
  transform: scale(1.1);
  transition-duration: 0.5s;
  opacity: 0.7;
}
.c {
  margin: 0 auto;
  overflow: hidden;
}
.c img {
  cursor: pointer;
  transition-duration: 0.5s;
}
.c img:hover {
  transform: scale(1.1);
  transition-duration: 0.5s;
  opacity: 0.7;
}
.main {
  margin: 0 auto;
  overflow: hidden;
}
.main img {
  width: 100%;
  cursor: pointer;
  border-radius: 0%;
  transition-duration: 0.3s;
}
.main img:hover {
  border-radius: 20%;
  transition-duration: 0.3s;
  opacity: 0.7;
}

span {
  color: #ff6347;
  font-weight: bold;
}

.main {
  display: block;
  position: relative;
  text-decoration: none;
}
.main img {
  display: block;
  width: 100%;
  transition: 0.3s;
}
.main:before {
  content: "Buy";
  display: block;
  color: #ff6347;
  line-height: 48px;
  width: 180px;
  border: solid 1px #ff6347;
  border-radius: 5px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1em;
  margin-left: -90px;
  opacity: 0;
  z-index: 3;
  transition: 0.3s;
  font-weight: bold;
  letter-spacing: 0.2em;
}
.main:hover img {
  filter: blur(2px);
}
.main:hover:before {
  opacity: 1;
  margin-top: -0.5em;
}
